.form {
	width: 100%;

	.row {
		width: 100%;
		display: flex;
		position: relative;
		padding: 20px 0;

		@media only screen and (max-width: 960px) {
			flex-direction: column;
		}
	}

	.row-inline {
		display: flex;

		.row {
			margin-left: 10px;

			&:first-child {
				margin: 0;
			}
		}

		@media only screen and (max-width: 576px) {
			flex-direction: column;

			.row {
				margin: 0;
			}
		}

	}

	.no-padding {
		padding: 0;
	}

	.header {
		text-align: center;
		font-size: 32px;
		width: 100%;
	}

	&-input {
		width: 100%;
		height: 40px;
		padding: 5px;
		box-sizing: border-box;
		@include HindFont;
		color: $secondary;
		border: 1px solid $border;
		border-radius: 3px;
		outline: 0;

		&[type=number] {
			border-radius: 0;
			text-align: center;
			border-left: 0;
			border-right: 0;

			&::-webkit-inner-spin-button {
				display: none;
			}
		}

		&:focus {
			border: 2px solid $blue;
		}
	}

	&-label {
		position: absolute;
		top: 0;
		left: -10px;
		color: $primary;
		@include LatoFont;
		text-transform: uppercase;
	}

	&-btn {
		margin-left: 5px;

		&:first-child {
			margin: 0;
		}

		@media only screen and (max-width: 960px) {
			margin: 20px 0;
		}
	}

	&-text {
		color: $primary;
		text-decoration: none;
		@include LatoFont;
		text-align: center;
		text-transform: uppercase;
		width: 100%;
		transition: all 250ms;

		&:hover {
			color: $blue;
		}
	}
}