body {
	background: $bgColor;
	color: $secondary;
	font-size: 14px;
	@include HindFont;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	color: $primary;
	@include LatoFont;
	text-transform: uppercase;
}

hr {
	margin: 30px 0;
	border-top: 1px solid $border;
}

.text-center {
	text-align: center;
}